@charset "UTF-8";
html {
  -ms-overflow-style: -ms-autohiding-scrollbar;
  text-size-adjust: 100%;
}

main {
  display: block;
}

details {
  display: block;
}

pre {
  overflow: auto;
}

progress {
  display: inline-block;
}

summary {
  display: block;
}

template {
  display: none;
}

*,
::before,
::after {
  border-style: solid;
  border-width: 0;
  box-sizing: border-box;
}

* {
  font-size: inherit;
  line-height: inherit;
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

audio :not([controls]) {
  display: none;
}

[aria-busy=true] {
  cursor: progress;
}

[aria-controls] {
  cursor: pointer;
}

[aria-disabled] {
  cursor: default;
}

[hidden] {
  display: none;
}

button {
  overflow: visible;
}

input {
  border-radius: 0;
  line-height: normal;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: button;
  cursor: pointer;
}
button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border: 0 none !important;
  padding: 0 !important;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number] {
  width: auto;
}

[type=search] {
  appearance: textfield;
}

button,
input,
select,
textarea {
  background-color: transparent;
  color: inherit;
  font-family: inherit;
  font-style: inherit;
  font-weight: inherit;
}

textarea {
  overflow: auto;
  resize: vertical;
}

img {
  border: 0;
}

svg:not(:root) {
  overflow: hidden;
}

audio,
canvas,
iframe,
img,
svg,
video {
  max-width: 100%;
  vertical-align: middle;
}

a {
  background-color: transparent;
}
a:active, a:hover {
  outline: 0;
}

abbr[title] {
  border-bottom: 1px dotted;
}

b,
strong {
  font-weight: bold;
}

code,
kbd,
pre,
samp {
  font-family: monospace;
  font-size: inherit;
}

dfn {
  font-style: italic;
}

nav ol,
nav ul {
  list-style: none;
}

small {
  font-size: 0.75em;
}

sub,
sup {
  font-size: 0.75em;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

[data-theme=light] {
  --color-background: #f1f3f3;
  --color-feature: #5f002f;
  --color-callout: #dee2e4;
  --color-text-shadow: white;
  --color-contrast: black;
  --color-mia-eyes:currentColor;
  --color-overlay: rgba(241, 243, 243, 0.8);
  --color-text: #1e4e5d;
  --color-text-light: #5a7077;
  --color-text-highlight: #316c7f;
  --color-border: #6d8187;
  --color-action: #95004a;
  --color-action-underline: rgba(149, 0, 74, 0.5);
  --color-action-hover: #5f002f;
  --shadow:var(--light, 0 1px 0);
}
[data-theme=light] [data-theme=invert] {
  --color-background: #151d20;
  --color-feature:var(--color-action-hover, #fac9e1);
  --color-callout: #30444b;
  --color-text-shadow: black;
  --color-contrast: white;
  --color-mia-eyes:var(--color-background, #151d20);
  --color-overlay: rgba(21, 29, 32, 0.8);
  --color-text: #9ab7c0;
  --color-text-light: #809196;
  --color-text-highlight: #5b8a99;
  --color-border: #5a7077;
  --color-action: #f48bbf;
  --color-action-underline: rgba(244, 139, 191, 0.5);
  --color-action-hover: #fac9e1;
  --shadow:var(--dark, 0 -1px 0);
}

[data-theme=dark] {
  --color-background: #151d20;
  --color-feature:var(--color-action-hover, #fac9e1);
  --color-callout: #30444b;
  --color-text-shadow: black;
  --color-contrast: white;
  --color-mia-eyes:var(--color-background, #151d20);
  --color-overlay: rgba(21, 29, 32, 0.8);
  --color-text: #9ab7c0;
  --color-text-light: #809196;
  --color-text-highlight: #5b8a99;
  --color-border: #5a7077;
  --color-action: #f48bbf;
  --color-action-underline: rgba(244, 139, 191, 0.5);
  --color-action-hover: #fac9e1;
  --shadow:var(--dark, 0 -1px 0);
}
[data-theme=dark] [data-theme=invert] {
  --color-background: #f1f3f3;
  --color-feature: #5f002f;
  --color-callout: #dee2e4;
  --color-text-shadow: white;
  --color-contrast: black;
  --color-mia-eyes:currentColor;
  --color-overlay: rgba(241, 243, 243, 0.8);
  --color-text: #1e4e5d;
  --color-text-light: #5a7077;
  --color-text-highlight: #316c7f;
  --color-border: #6d8187;
  --color-action: #95004a;
  --color-action-underline: rgba(149, 0, 74, 0.5);
  --color-action-hover: #5f002f;
  --shadow:var(--light, 0 1px 0);
}

[data-theme] {
  background-color: var(--color-background, #f1f3f3);
  color: var(--color-text, #1e4e5d);
  text-shadow: var(--shadow, 0 1px 0) var(--color-text-shadow, white);
}

@font-face {
  font-family: "Dank Mono";
  font-style: normal;
  font-weight: normal;
  src: url("../fonts/dankmono/dankmono-regular-webfont.woff2") format("woff2"), url("../fonts/dankmono/dankmono-regular-webfont.woff") format("woff");
}
@font-face {
  font-family: "Dank Mono";
  font-style: italic;
  font-weight: normal;
  src: url("../fonts/dankmono/dankmono-italic-webfont.woff2") format("woff2"), url("../fonts/dankmono/dankmono-italic-webfont.woff") format("woff");
}
@font-face {
  font-family: "amp";
  font-style: italic;
  font-weight: normal;
  src: url("data:application/font-woff2;charset=utf-8;base64,") format("woff2"), url("data:application/font-woff;charset=utf-8;base64,") format("woff");
}
:root {
  font-family: freight-text-pro, Baskerville, Palatino, Cambria, Georgia, serif;
  font-size: calc(16px + 0.5vw);
  line-height: 1.4;
}

h1 {
  font-size: calc(1.4rem + 4.5vw);
}

a:link, a:visited {
  color: var(--color-action, #95004a);
  text-decoration-color: var(--color-action-underline, rgba(149, 0, 74, 0.5));
  text-decoration-skip-ink: auto;
}
a:hover, a:focus, a:active {
  color: var(--color-action-hover, #5f002f);
  text-decoration-color: currentColor;
}
a[href*="://"]::after {
  content: "↗";
  font-family: "Dank Mono", Consolas, Menlo, Monaco, "Lucida Console", "Liberation Mono", "DejaVu Sans Mono", "Bitstream Vera Sans Mono", "Courier New", monospace;
  color: var(--color-text-light, #5a7077);
  font-size: 0.875em;
}

[data-hidden] {
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(1px 1px 1px 1px);
  height: 1px;
  overflow: hidden;
  pointer-events: none;
  position: absolute;
  width: 1px;
}

.amp {
  font-family: amp, freight-text-pro, Baskerville, Palatino, Cambria, Georgia, serif;
  --color-amp:var(--color-text-highlight, #316c7f);
  color: var(--color-amp, var(--color-text-highlight, #316c7f));
  font-style: italic;
}

:root {
  --grid-content: [full-start left-start] [content-start] minmax(0, 1fr) [main-start] minmax(0, 9rem) [half-left] minmax(0, 9rem) [left-end midpoint right-start] minmax(0, 9rem) [half-right] minmax(0, 9rem) [main-end] minmax(0, 1fr) [content-end] [right-end full-end];
  --grid-full: [full-start left-start] 0.7rem [content-start] minmax(0, 1fr) [main-start] minmax(0, 9rem) [half-left] minmax(0, 9rem) [left-end midpoint right-start] minmax(0, 9rem) [half-right] minmax(0, 9rem) [main-end] minmax(0, 1fr) [content-end] 0.7rem [right-end full-end];
  --grid-small: [full-start left-start] 0.7rem [content-start] minmax(0, 1fr) [main-start right-start] minmax(0, 9rem) [half-left] minmax(0, 9rem) [midpoint] minmax(0, 9rem) [half-right] minmax(0, 9rem) [left-end main-end] minmax(0, 1fr) [content-end] 0.7rem [right-end full-end];
  --grid-main: [full-start left-start main-start] minmax(0, 9rem) [half-left] minmax(0, 9rem) [left-end midpoint right-start] minmax(0, 9rem) [half-right] minmax(0, 9rem) [main-end right-end full-end];
  --grid-main-left: [full-start left-start] 0.7rem [content-start] minmax(0, 1fr) [main-start] minmax(0, 9rem) [half-left] minmax(0, 9rem) [left-end midpoint right-start] minmax(0, 9rem) [half-right] minmax(0, 9rem) [main-end right-end full-end];
  --grid-main-right: [full-start left-start main-start] minmax(0, 9rem) [half-left] minmax(0, 9rem) [left-end midpoint right-start] minmax(0, 9rem) [half-right] minmax(0, 9rem) [main-end] minmax(0, 1fr) [content-end] 0.7rem;
  --grid-left: [full-start left-start] 0.7rem [content-start] minmax(0, 1fr) [main-start] minmax(0, 9rem) [half-left] minmax(0, 9rem) [left-end midpoint right-start content-end main-end right-end full-end];
  --grid-right: [full-start left-start content-start main-start left-end midpoint right-start] minmax(0, 9rem) [half-right] minmax(0, 9rem) [main-end] minmax(0, 1fr) [content-end] 0.7rem [right-end full-end];
  --grid-cards:repeat(auto-fill, minmax(14.4rem, 1fr));
}

[data-layout=body] {
  grid-template-rows: auto 1fr;
  padding: 1.4rem 0;
  min-height: 100vh;
}

[data-section] {
  --top: 0.7rem;
  --right: 0.7rem;
  --bottom: 0.7rem;
  --left: 0.7rem;
  border-top: 0.35rem solid var(--color-border, #6d8187);
  display: grid;
}

[data-area],
[data-content] {
  --start: main-start;
  --end: main-end;
  --area: var(--start, main-start) / var(--end, main-end);
  --grid: var(--grid-main, [full-start left-start main-start] minmax(0, 9rem) [half-left] minmax(0, 9rem) [left-end midpoint right-start] minmax(0, 9rem) [half-right] minmax(0, 9rem) [main-end right-end full-end]);
  grid-column: var(--area, main);
}

[data-content] {
  padding: var(--top, 0.7rem) var(--right, 0.7rem) var(--bottom, 0.7rem) var(--left, 0.7rem);
}

[data-main] {
  margin-bottom: 2.8rem;
}

[data-section=title] {
  font-family: "Dank Mono", Consolas, Menlo, Monaco, "Lucida Console", "Liberation Mono", "DejaVu Sans Mono", "Bitstream Vera Sans Mono", "Courier New", monospace;
  --top: 0;
  font-size: 0.8451542547rem;
  text-align: right;
}

[data-title=title] {
  color: var(--color-text-highlight, #316c7f);
  display: inline-block;
  font-style: italic;
  font-weight: normal;
}

[data-area] {
  grid-template-columns: var(--grid, var(--grid-main, [full-start left-start main-start] minmax(0, 9rem) [half-left] minmax(0, 9rem) [left-end midpoint right-start] minmax(0, 9rem) [half-right] minmax(0, 9rem) [main-end right-end full-end]));
}

[data-area=full] {
  --area: full;
  --grid: var(--grid-small, [full-start left-start] 0.7rem [content-start] minmax(0, 1fr) [main-start right-start] minmax(0, 9rem) [half-left] minmax(0, 9rem) [midpoint] minmax(0, 9rem) [half-right] minmax(0, 9rem) [left-end main-end] minmax(0, 1fr) [content-end] 0.7rem [right-end full-end]);
}
@media (min-width: 40.5em) {
  [data-area=full] {
    --grid: var(--grid-full, [full-start left-start] 0.7rem [content-start] minmax(0, 1fr) [main-start] minmax(0, 9rem) [half-left] minmax(0, 9rem) [left-end midpoint right-start] minmax(0, 9rem) [half-right] minmax(0, 9rem) [main-end] minmax(0, 1fr) [content-end] 0.7rem [right-end full-end]);
  }
}

[data-area=content] {
  --area: content;
  --grid: var(--grid-content, [full-start left-start] [content-start] minmax(0, 1fr) [main-start] minmax(0, 9rem) [half-left] minmax(0, 9rem) [left-end midpoint right-start] minmax(0, 9rem) [half-right] minmax(0, 9rem) [main-end] minmax(0, 1fr) [content-end] [right-end full-end]);
}

[data-area=left] {
  --area: left;
  --grid: var(--grid-left, [full-start left-start] 0.7rem [content-start] minmax(0, 1fr) [main-start] minmax(0, 9rem) [half-left] minmax(0, 9rem) [left-end midpoint right-start content-end main-end right-end full-end]);
}

[data-area=right] {
  --area: right;
  --grid: var(--grid-right, [full-start left-start content-start main-start left-end midpoint right-start] minmax(0, 9rem) [half-right] minmax(0, 9rem) [main-end] minmax(0, 1fr) [content-end] 0.7rem [right-end full-end]);
}

[data-area=main-left] {
  --area: left-start / main-end;
  --grid: var(--grid-main-left, [full-start left-start] 0.7rem [content-start] minmax(0, 1fr) [main-start] minmax(0, 9rem) [half-left] minmax(0, 9rem) [left-end midpoint right-start] minmax(0, 9rem) [half-right] minmax(0, 9rem) [main-end right-end full-end]);
}

[data-area=main-right] {
  --area: main-start / right-end;
  --grid: var(--grid-main-right, [full-start left-start main-start] minmax(0, 9rem) [half-left] minmax(0, 9rem) [left-end midpoint right-start] minmax(0, 9rem) [half-right] minmax(0, 9rem) [main-end] minmax(0, 1fr) [content-end] 0.7rem);
}

[data-flow=grid] {
  display: grid;
}

[data-flow=flex] {
  display: flex;
  flex-wrap: wrap;
}

[data-flow=split] {
  display: grid;
  grid-column-gap: 1.4rem;
  grid-template: auto/repeat(2, minmax(0, 1fr));
}

[data-section=nav] [data-content] {
  margin-top: -0.35rem;
  padding: 0;
}

[data-nav] {
  --color-amp:var(--color-text, #1e4e5d);
  font-family: "Dank Mono", Consolas, Menlo, Monaco, "Lucida Console", "Liberation Mono", "DejaVu Sans Mono", "Bitstream Vera Sans Mono", "Courier New", monospace;
  display: inline-block;
  border-top: 0.35rem solid transparent;
  font-size: 0.7142857143rem;
  font-style: italic;
  padding: 0.175rem 0.7rem;
}
[data-nav][data-active] {
  --color-amp:var(--color-action, #95004a);
  border-color: var(--color-action, #95004a);
  color: var(--color-text, #1e4e5d);
  font-style: normal;
  text-decoration: none;
}

[data-layout~=banner] {
  display: grid;
  font-style: italic;
  grid-column: full;
  margin-bottom: 1.4rem;
  overflow-x: hidden;
}

.banner-content {
  background: var(--color-overlay, rgba(241, 243, 243, 0.8));
  align-content: center;
  display: grid;
  grid-area: 1/1/-1/-1;
  grid-template: "icon" auto "title" auto "sub" auto/auto;
  justify-self: center;
  margin: 1.4rem;
  text-align: center;
}
@media (min-width: 40.5em) {
  .banner-content {
    grid-template: "icon title" auto "icon sub" auto/auto 1fr;
    margin: calc(2.8rem + 6vmin) 1.4rem;
    text-align: left;
  }
}

.banner-icon {
  grid-area: icon;
}
.banner-icon [data-icon] {
  height: calc(2.8rem + 6vmin);
  width: calc(2.8rem + 6vmin);
}

.banner-title {
  color: var(--color-text-highlight, #316c7f);
  grid-area: title;
}

.subtitle {
  font-family: "Dank Mono", Consolas, Menlo, Monaco, "Lucida Console", "Liberation Mono", "DejaVu Sans Mono", "Bitstream Vera Sans Mono", "Courier New", monospace;
  color: var(--color-text-light, #5a7077);
  grid-area: sub;
  line-height: 1;
  font-size: 0.8451542547rem;
}

.banner-fancy {
  align-items: center;
  display: grid;
  justify-items: center;
  grid-area: 1/1/-1/-1;
  padding-bottom: 1.4rem;
  position: relative;
  z-index: -1;
}

[data-dot] {
  height: calc(6px + 1vmin);
  width: calc(6px + 1vmin);
  --dot: var(--dot-color-6);
  --opacity: 0.35;
  background: var(--dot);
  border-radius: 100%;
  fill: var(--dot);
  grid-area: var(--area);
  opacity: var(--opacity);
  transform: scale(var(--scale));
  --dot-color-1: var(--color-_brand-accent-light-4, #f7aad0);
  --dot-color-2: var(--color-_brand-main-dark-5, #1e4e5d);
  --dot-color-3: var(--color-_brand-accent-dark-2, #e60073);
  --dot-color-4: var(--color-_brand-main-dark-3, #316c7f);
  --dot-color-5: var(--color-_brand-main-light-1, #85a8b3);
  --dot-color-6: var(--color-_brand-accent-dark-2, #e60073);
}

[data-dot="1"] {
  --dot: var(--dot-color-1);
}

[data-dot="2"] {
  --dot: var(--dot-color-2);
}

[data-dot="3"] {
  --dot: var(--dot-color-3);
}

[data-dot="4"] {
  --dot: var(--dot-color-4);
}

[data-dot="5"] {
  --dot: var(--dot-color-5);
}

@keyframes blip {
  12%, 37%, 95% {
    opacity: 0;
  }
  21%, 40%, 63% {
    opacity: 1;
  }
}
[data-content~=hero] {
  padding: 0;
}

.hero-caption {
  font-family: "Dank Mono", Consolas, Menlo, Monaco, "Lucida Console", "Liberation Mono", "DejaVu Sans Mono", "Bitstream Vera Sans Mono", "Courier New", monospace;
  font-size: 0.7142857143rem;
  font-style: italic;
  padding: 0.175rem 0.175rem 0.35rem;
}

.footer-hr {
  border: none;
  grid-column: full;
  margin-top: 1.4rem;
}

[data-section=footer] {
  font-family: "Dank Mono", Consolas, Menlo, Monaco, "Lucida Console", "Liberation Mono", "DejaVu Sans Mono", "Bitstream Vera Sans Mono", "Courier New", monospace;
  --top: 0.175rem;
  font-size: 0.7142857143rem;
}
[data-section=footer] p {
  margin-bottom: 0.35rem;
}

[data-btn] {
  background: var(--color-action, #95004a);
  border-radius: 0.1em;
  color: var(--color-background, #f1f3f3);
  padding: 0 0.35rem;
}
[data-btn]:hover, [data-btn]:focus, [data-btn]:active {
  background: var(--color-action-hover, #5f002f);
  color: var(--color-background, #f1f3f3);
}

[data-layout=card-groups] {
  margin: 1.4rem 0;
}

[data-layout=cards] {
  display: grid;
  font-size: 0.8451542547rem;
  grid-gap: 0.35rem;
  grid-template-columns: var(--grid-cards, repeat(auto-fill, minmax(14.4rem, 1fr)));
  list-style: none;
  padding: 0 0.7rem 0 0.35rem;
}

[data-card-group] {
  display: grid;
  grid-template: "grouper list" auto/auto minmax(0, 1fr);
  margin-bottom: 1.4rem;
}

[data-cards=grouper] {
  font-family: "Dank Mono", Consolas, Menlo, Monaco, "Lucida Console", "Liberation Mono", "DejaVu Sans Mono", "Bitstream Vera Sans Mono", "Courier New", monospace;
  background: var(--color-contrast, black) linear-gradient(to top, var(--color-action-hover, #5f002f), transparent 80%);
  color: var(--color-background, #f1f3f3);
  font-size: 0.7142857143rem;
  grid-area: grouper;
  padding: 0.7rem 0;
  text-shadow: none;
  writing-mode: vertical-rl;
}

[data-card=item] {
  --feature: -0.006turn;
  display: grid;
  grid-template: minmax(0, 1fr)/minmax(0, 1fr);
}
[data-card=item]:nth-child(2n) {
  --feature: 0.005turn;
}
[data-card=item]:nth-child(3n) {
  --feature: -0.007turn;
}
[data-card=item]:nth-child(4n) {
  --feature: 0.004turn;
}
[data-card=item][data-feature] {
  background: linear-gradient(to bottom, var(--color-text, #1e4e5d), var(--color-feature, #5f002f));
  margin: -0.175rem;
  padding: 0.175rem;
}

[data-card=inner] {
  background: var(--color-callout, #dee2e4);
  padding: 0.35rem 0.525rem;
}
[data-feature] [data-card=inner] {
  background: var(--color-background, #f1f3f3);
  border: 1px solid var(--color-action, #95004a);
  transform: rotate(var(--feature, -0.006turn));
}

[data-card=title] {
  font-style: italic;
}

[data-card=meta] {
  --color-action:var(--color-text-light, #5a7077);
  --color-action-underline:var(--color-border, #6d8187);
  font-family: "Dank Mono", Consolas, Menlo, Monaco, "Lucida Console", "Liberation Mono", "DejaVu Sans Mono", "Bitstream Vera Sans Mono", "Courier New", monospace;
  color: var(--color-border, #6d8187);
  font-size: 0.7142857143rem;
  overflow: hidden;
  padding-bottom: 0.175rem;
  text-overflow: ellipsis;
  white-space: nowrap;
}

[data-erase] mark {
  background: none;
}

[data-erase=on] :not(mark) {
  color: transparent;
  text-shadow: none;
}

[data-toggle=erase] {
